import React, { useState,useEffect } from 'react'
import { Button, Container,Table } from 'reactstrap'
import ReactPaginate from 'react-paginate';
import "./AllCustomers.css"
import { TablePagination } from '@material-ui/core';
import base_url from './api/bootapi';
import { Link } from 'react-router-dom';
import {withRouter} from "react-router-dom";

function ViewCustomer (props) {

  console.warn("props",props.match.params.firstName)

  useEffect (async () =>{ 
    let result =await fetch(`${base_url}/customer/details/${props.match.params.firstName}`);
    result =  await result.json();
    setperson(result)
},[])

const[person,setperson] = useState([]);

    

    //*************** */ for Pagination
    const [pagenumber, setPageNumber] = useState(0)
    const usersPage = 10
    const pageVisited = pagenumber * usersPage
    const pageCount = Math.ceil(person.length/usersPage)
    const changePage=({selected})=>{
        setPageNumber(selected)
    }
    const customerlist = person.slice(pageVisited, pageVisited + usersPage).map(customer => {
        
      return <tr key={customer.id}>
        <td>{customer.firstName}</td>
        <td>{customer.transactionEffectiveDate}</td>
        <td>{customer.creditAmountReceived}</td>
        <td>{customer.debitAmountReceived}</td>
        <td>{customer.totalAmountPending}</td>
      </tr>
    });
 // ************************************************
   
    return(
     <div>
       
        <Container fluid>
        <h3 align="center">Customer Transactions</h3>
        <br></br>
          <Table className="mt-4">
            <thead>
            <tr>
              <th width="10%">FirstName</th>
              <th width="10%">Received Date</th>
              <th width="10%">Credit Amount</th>
              <th width="10%">Debit Amount</th>
              <th width="10%">AmountPending</th>
            </tr>
            </thead>
            <tbody>
            {customerlist}
            <br />
           
            </tbody>
          </Table>
          <ReactPaginate
            rowsPer
            previousLabel={"Previous"}
            nextLabel={"Next"}
            pageCount={pageCount} 
            onPageChange={changePage}
            containerClassName={"paginationBttns"}
            previousLinkClassName={"previousBttn"}
            nextLinkClassName={"nextBttn"}
            disabledClassName={"paginationDisabled"}
            activeClassName={"paginationActive"}
            />
        </Container>
        
     </div>
    
);
   

};
export default  withRouter(ViewCustomer);