import React,{useEffect, useState} from 'react'
import { Button, Container, Form, FormGroup, Input,Col,Table,Row } from 'reactstrap'
import axios from "axios";
import base_url from './api/bootapi';
import { toast } from 'react-toastify';
import {useLocation,withRouter} from "react-router-dom";
import SearchBar from './SearchBar';
import { Link } from 'react-router-dom';
import Receipt from './Receipt';



const CashReceipt =(props) => {

    console.warn("props",props.match.params.firstName)

    const getAllCustomers = () => {
        
        axios.get(`${base_url}/customer`).then(
            (response) => {
                console.log(response.data);
                if(response.data === []){
                toast.success("customer has been loaded",{
                   position: "bottom-center", 
                });}
                setCustomer(response.data);
            },

            (error) => {
                console.log(error);
                toast.error("something went wrong",{
                    position: "bottom-center", 
                 });
            }
        );
 };

useEffect (() =>{
    getAllCustomers();
    },[]);

 const[customer,setCustomer]= useState([]);
    
   
return (
         <div>
               <h1 className="text-center">Add CashReceipt</h1>
            <br></br>
                 <SearchBar  data={customer}/>
          </div>
    );
};
export default  withRouter(CashReceipt);