import React from "react";
import axios from "axios";
import {
    Card,CardBody, CardSubtitle, Button, Container} from 'reactstrap';
import base_url from "./api/bootapi";
import { toast } from "react-toastify";
import { Link } from 'react-router-dom';



  const Customer = ({ course, update,rahil})=>{

    
    const deleteCourse = (id) => {
        axios.delete(`${base_url}/customer/${id}`).then(
            (response)=>{
                toast.success("deleted successfully");
                update(id);
            },
            (error)=>{
                toast.error("not deleted");

            }
        )
    }

    return(
        <Card className="text-center">
            <CardBody>
                <CardSubtitle className="font-weight-bold">{course.name}</CardSubtitle>
                <Container className="text-center">
                   <Link to={`/update-customer/${course.name}`}><Button color="warning" >Add Entry</Button></Link>
                    &nbsp;&nbsp;&nbsp;
                   <Link to={`/view-customer/${course.name}`}><Button color="success">View</Button></Link>
                    &nbsp;&nbsp;&nbsp;
                    <Button color="danger" onClick={() => { deleteCourse(course.id); }}>Delete</Button>
                    &nbsp;&nbsp;&nbsp;
                    <Link to={`/update-name/${course.id}`}><Button color="success">Update</Button></Link>
                </Container>
            </CardBody>
        </Card>
    );
  }
  export default Customer;