import React, { useState, useEffect } from 'react'
import {Row, Col, Container,Table,Button} from "reactstrap";
import base_url from './api/bootapi';
import axios from "axios";
import { toast } from 'react-toastify';
import ReactPaginate from 'react-paginate';
import "./AllCustomers.css"


const Balance = () => {

  const data = {
    "id": 1,
    "firstName": "RATAN MAJUR",
    "lastName": null,
    "transactionEffectiveDate": null,
    "totalAmountPending": 0,
    "description": null,
    "creditAmountReceived": 0,
    "debitAmountReceived": 0,
    "receiptLink": null,
    "paymentLink": null,
    "fromDate": "2021-04-01",
    "toDate": "2022-03-31"
  }

  useEffect(() => {
    document.title = "Cash Book";

  }, []);

  const [customers, setCustomers] = useState([]);
  const [balance, setBalance] = useState([]);
  const[datewise,setDatewise] = useState(data);
  

 const Balance = () => {
    axios.get(`${base_url}/customer/balance`).then(
      (response) => {
        console.log(response.data);
        setCustomers(response.data);
      },

      (error) => {
        console.log(error);
        toast.error("something went wrong", {
          position: "bottom-center",
        });
      }
    );
  }

  const EndingBalance = () => {
    axios.get(`${base_url}/customer/endingBalance`).then(
      (response) => {
        console.log(response.data);
        setBalance(response.data);
      },

      (error) => {
        console.log(error);
        toast.error("something went wrong", {
          position: "bottom-center",
        });
      }
    )
  }
// for getting details on selected date
  const datewiseCustomers = (data) => {
    if (data.fromDate > data.toDate) {
      toast.error("From Date Should Be Bigger", {
        position: "bottom-center",
      })
    } else {
      axios.put(`${base_url}/customer/datewiseDetails`, data).then(
        (response) => {
          console.log(response.data);
          if (response.data.length === 0) {
            toast.error("No Record Found", {
              position: "bottom-center",
            })
          }
          setCustomers(response.data);
        },

        (error) => {
          console.log(error);
          toast.error("something went wrong", {
            position: "bottom-center",
          });
        }
      );
    }
  }

  const EndingDateWiseBalance = (data) => {
    axios.put(`${base_url}/customer/getListFromDate`, data).then(
      (response) => {
        console.log(response.data);
        setBalance(response.data);
      },
      (error) => {
        console.log(error);
        toast.error("something went wrong", {
          position: "bottom-center",
        });
      }
    )
  }

// pagination
const [pagenumber, setPageNumber] = useState(0)
   
const usersPage = 9
const pageVisited = pagenumber * usersPage
const pageCount = Math.ceil(customers.length/usersPage)
const changePage=({selected})=>{
    setPageNumber(selected)
}
 

  useEffect(() => {
    Balance();
    EndingBalance();
  }, []);

  const customerlist = customers.filter((val)=>{
    if (val.creditAmountReceived !== 0) {
    return val
  }
  }).map(customer => {
      return <tr key={customer.id}>
      <td>{customer.transactionEffectiveDate}</td>
       <td style={{ whiteSpace: 'nowrap' }}>{customer.firstName}</td>
      <td>{customer.creditAmountReceived}</td>
    </tr>
  });

  const customerdebitlist = customers.filter((val) => {
    if (val.debitAmountReceived !== 0) {
      return val
    }
  }).map(customer => {
     return <tr key={customer.id}>
       <td>{customer.transactionEffectiveDate}</td>
       <td style={{ whiteSpace: 'nowrap' }}>{customer.firstName}</td>
      <td>{customer.debitAmountReceived}</td>
    </tr>
  });

  const handleForm = (e) => {
    console.log(datewise);
    datewiseCustomers(datewise);
    EndingDateWiseBalance(datewise)
    e.preventDefault();
  }


  return (
    <div>

      <br></br>
      <div>
        <form  onSubmit={handleForm} class="form-inline">
          <label for="endingBalance">Ending Balance:</label>
          <input type="text" id="endingBalance" readOnly="true" defaultValue={balance.endingBalance} readOnly="true" style={{ marginLeft: 5 }} />
          <label for="todayDate" style={{ marginLeft: 35 }}>Date:</label>
          <input type="date" id="todayDate" defaultValue={balance.todayDate} style={{ marginLeft: 5 }} onChange={(e) => {
            setDatewise({ ...datewise, fromDate: e.target.value })
          } }/>

          <Container  >
            <Button type="submit" color="success" >Search</Button>
          </Container>
        </form>
      </div>
      <br></br>
      <Row>
        <Col md={6}>
          <Table className="mt-4">
            <thead>
              <tr>
              <th width="15%">Date</th>
                <th width="20%">Name</th>
                <th width="15%">Rs</th>
              </tr>
            </thead>
            <tbody>
              {customerlist}
            </tbody>
          </Table>
        </Col>
        <Col md={6} >
          <Table className="mt-4 offset-2">
            <thead>
              <tr>
              <th width="15%">Date</th>
                <th width="20%">Name</th>
                <th width="15%">Rs</th>
              </tr>
            </thead>
            <tbody>
              {customerdebitlist}
            </tbody>
          </Table>
        </Col>
      </Row>
       
       
    </div>
  )
}
export default Balance;