import React,{useEffect, useState} from 'react'
import { Button, Container, Form, FormGroup, Input } from 'reactstrap'
import axios from "axios";
import base_url from './api/bootapi';
import { toast } from 'react-toastify';
import {withRouter} from "react-router-dom";


const UpdateCustomer =(props) => {

    console.warn("props",props.match.params.firstName,props.match.params.lastName)

    
    useEffect (async () =>{ 
        let result =await fetch(`${base_url}/customer/${props.match.params.firstName}`);
        result =  await result.json();
        setperson(result)
    },[])
    const[customer,setCustomer] = useState([]);

    const[person,setperson] = useState([]);
    

   
    //form handler
    const handleForm=(e)=>{
        console.log(person);
        postDatatoServer(person);
        
       
        e.target.reset()
       
        e.preventDefault();
    }

    //creating function t post data to server
    const postDatatoServer= (data) =>{
        if(data.creditAmountReceived === 0 && data.debitAmountReceived === 0){
            toast.error("Please enter credit or debit Amount",{
                position:"bottom-center",
            });
        }else{
        axios.put(`${base_url}/customer/`,data).then(
            (response)=>{
                console.log(response);
                setperson({...person, totalAmountPending: response.data.totalAmountPending });
                document.getElementById("totalAmountPending").value= response.data.totalAmountPending
                  toast.success("Entry has been added successfully",{
                    position:"bottom-center",
                });
                
              
            },(error)=>{
                console.log(error);
                console.log("error");
            }
        )}
    }
        
//for moving to next input box on pressing enter

    let input = document.getElementById("description");
    let creditInput = document.getElementById("creditAmountReceived");
    if (input != null && creditInput != null) {
        input.addEventListener("keydown", function (event) {
            if (event.keyCode === 13) {
                event.preventDefault();
                document.getElementById("creditAmountReceived").focus()
            }
        });
        creditInput.addEventListener("keydown", function (event) {
            if (event.keyCode === 13) {
                document.getElementById("debitAmountReceived").focus();
            }
        })
    }
//for moving  back  to previous input box on pressing escape
    let inputDebit = document.getElementById("debitAmountReceived");
    let inputCredit = document.getElementById("creditAmountReceived");
    if (input != null && creditInput != null) {
        inputDebit.addEventListener("keydown", function (event) {
            if (event.keyCode === 27) {
                event.preventDefault();
                document.getElementById("creditAmountReceived").focus()
            }
        });
        creditInput.addEventListener("keydown", function (event) {
            if (event.keyCode === 27) {
                document.getElementById("description").focus();
            }
        })
    }
    
     return (
        <div>
             
            <h1 className="text-center">Add Entry</h1>
            <br></br>
           
            <Form onSubmit={handleForm}>
                <FormGroup>
                    <label for="firstName" style={{ width:300 }}>FirstName</label>
                    <Input type="text" defaultValue={person.firstName} readOnly="true" style={{ width:300 }}/>
                </FormGroup>
                <br></br>

               
                
                 <FormGroup>
                    <label for="description" style={{ width:300 }}>Description</label>
                    <Input type="textarea" placeholder="Enter Description"  name="description" id="description"  autoFocus   maxlength="110"
                     onChange={(e) => {
                        setperson({...person, description: e.target.value });
                    }}  style={{ width:300 }}/>
                 </FormGroup>
                 <br></br>

                 <FormGroup>
                    <label for="totalAmountPending" style={{ width:300 }}>Total Amount Pending</label>
                    <Input type="text" defaultValue={person.totalAmountPending}   readOnly="totalAmountPending" id="totalAmountPending" 
                     style={{ width:300 }}/>
                 </FormGroup>
                 <br></br>

                 <FormGroup>
                    <label for="creditAmountReceived" style={{ width:300 }}>Credit Amount</label>
                    <Input  placeholder="Enter Credit Amount"  name="creditAmountReceived" id="creditAmountReceived" 
                    onChange={(e) => {
                        setperson({...person, creditAmountReceived: e.target.value });
                    }}  style={{ width:300 }}/>
                 </FormGroup>
                 <br></br>

                 <FormGroup>
                    <label for="debitAmountReceived" style={{ width:300 }}>Debit Amount</label>
                    <Input type="number" placeholder="Enter Debit Amount"  name="debitAmountReceived" id="debitAmountReceived" 
                    onChange={(e) => {
                        setperson({...person, debitAmountReceived: e.target.value });
                    }}  style={{ width:300 }}/>
                 </FormGroup>
                 <br></br>

                 <FormGroup>
                    <label for="transactionEffectiveDate" style={{ width:300 }}>Transaction Date</label>
                    <Input type="date" placeholder="Enter Date" defaultValue={person.transactionEffectiveDate} readOnly="true" name="transactionEffectiveDate" id="transactionEffectiveDate" required
                     style={{ width:300 }}/>
                 </FormGroup>

                 <Container>
                     <Button  type="submit" color="success" >Add Entry</Button>
                     &nbsp;&nbsp;&nbsp;
                     <Button type ="reset" color="warning">Clear</Button>
                 </Container>
            </Form>
            </div>
    );
};
export default  withRouter(UpdateCustomer);