import React, { useState, useEffect } from 'react'
import Customer from './Customer'
import base_url from './api/bootapi';
import axios from "axios";
import { toast } from 'react-toastify';
import ReactPaginate from 'react-paginate';
import "./AllCustomers.css"

const AllCustomers = () => {

    useEffect(() => {
        document.title = "All Customers";

    }, []);

    //function to call server
    const getAllCustomers = () => {

        axios.get(`${base_url}/customer`).then(
            (response) => {
                console.log(response.data);
                if (response.data === []) {
                    toast.success("customer has been loaded", {
                        position: "bottom-center",
                    });
                }
                setCustomers(response.data);
            },

            (error) => {
                console.log(error);
                toast.error("something went wrong", {
                    position: "bottom-center",
                });
            }
        );
    };

    useEffect(() => {
        getAllCustomers();

    }, []);

    const [customers, setCustomers] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
   
   
    
    const updateCustomer = (id) => {
        setCustomers(customers.filter((c) => c.id !== id));

    }

//*************** */ for Pagination
    const [pagenumber, setPageNumber] = useState(0)
    const usersPage = 5
    const pageVisited = pagenumber * usersPage
    const pageCount = Math.ceil(customers.length/usersPage)
    const changePage=({selected})=>{
        setPageNumber(selected)
    }
    const displayUsers = customers.slice(pageVisited, pageVisited + usersPage).map((item) => {
        return (
            <Customer key={item.id} course={item} update={updateCustomer} />

        );
    })

    const displaySingleUser = customers.filter((val) => {
        
        if (val.name.toUpperCase().includes(searchTerm.toUpperCase())) {
            return val
        }
    }).map((item) => {
        return (
            <Customer key={item.id} course={item} update={updateCustomer} />

        );
    })
 // ************************************************

    return (
        <div>

            <h1 className="text-center">AllCustomers</h1>
            <input type="text" placeholder="Search..." onChange={e => { setSearchTerm(e.target.value) }} />
            {searchTerm === ""?  displayUsers:displaySingleUser}
          
            <br/>
            <ReactPaginate
            previousLabel={"Previous"}
            nextLabel={"Next"}
            pageCount={pageCount} 
            onPageChange={changePage}
            containerClassName={"paginationBttns"}
            previousLinkClassName={"previousBttn"}
            nextLinkClassName={"nextBttn"}
            disabledClassName={"paginationDisabled"}
            activeClassName={"paginationActive"}
            />
            </div>
    );


};
export default AllCustomers;