import React, { useState, useEffect } from 'react'
import { Container, Row, Col, Table, Button } from "reactstrap";
import base_url from './api/bootapi';
import axios from "axios";
import { toast } from 'react-toastify';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { DataGrid } from '@material-ui/data-grid';

const CashBook = () => {

  useEffect(() => {
    document.title = "Ledger";

  }, []);

  const data = {
    "id": 1,
    "firstName": "RATAN MAJUR",
    "lastName": null,
    "transactionEffectiveDate": null,
    "totalAmountPending": 0,
    "description": null,
    "creditAmountReceived": 0,
    "debitAmountReceived": 0,
    "receiptLink": null,
    "paymentLink": null,
    "fromDate": "2021-04-01",
    "toDate": "2022-03-31"
  }

  const [dates, setDates] = useState([]);
  const [isChanged, setisChanged] = useState(false)
  const [dateWiseBalance, setDateWiseBalance] = useState([])
  const [datewise, setDatewise] = useState(data);
  const [customer, setCustomer] = useState([]);
  const [filteredData, setFilteredData] = useState([])


  const getAllCustomers = () => {
    axios.get(`${base_url}/customer`).then(
      (response) => {
        console.log(response.data);
        if (response.data === []) {
          toast.success("customer has been loaded", {
            position: "bottom-center",
          });
        }
        setCustomer(response.data);
      },
      (error) => {
        console.log(error);
        toast.error("something went wrong", {
          position: "bottom-center",
        });
      }
    );
  };

  useEffect(() => {
    getAllCustomers();
  }, [])


  const datewiseCustomers = (data) => {
    if (data.fromDate > data.toDate) {
      toast.error("From Date Should Be Bigger", {
        position: "bottom-center",
      })
    } else {
      axios.put(`${base_url}/customer/datewise`, data).then(
        (response) => {
          console.log(response.data);
          if (response.data.length === 0) {
            toast.error("No Record Found", {
              position: "bottom-center",
            })
          }
          setDates(response.data);
          setisChanged(true)
        },

        (error) => {
          console.log(error);
          toast.error("something went wrong", {
            position: "bottom-center",
          });
        }
      );
    }
  }

  const EndingDateWiseBalance = (data) => {
    axios.put(`${base_url}/customer/endingDateWiseBalance`, data).then(
      (response) => {
        console.log(response.data);
        setDateWiseBalance(response.data);
      },
      (error) => {
        console.log(error);
        toast.error("something went wrong", {
          position: "bottom-center",
        });
      }
    )
  }

  const datelist = dates.filter((val)=>{ 
    if (val.creditAmountReceived !== 0) {
    return val
  }}).map(customer => {
   
    return <tr key={customer.id}>
    <td>{customer.transactionEffectiveDate}</td>
       <td style={{ whiteSpace: 'nowrap' }}>{customer.firstName}</td>
      <td>{customer.creditAmountReceived}</td>
    </tr>
  });

  const datedebitlist = dates.filter((val) => {
    if (val.debitAmountReceived !== 0) {
      return val
    }
  }).map(customer => {

    return <tr key={customer.id}>
       <td>{customer.transactionEffectiveDate}</td>
       <td style={{ whiteSpace: 'nowrap' }}>{customer.firstName}</td>
      <td>{customer.debitAmountReceived}</td>
     
     
    </tr>
  });


  const handleForm = (e) => {
    console.log(datewise);
    datewiseCustomers(datewise);
    EndingDateWiseBalance(datewise)
    e.preventDefault();
  }
  
  
  const [inputValue, setInputValue] = React.useState('');
  

  return (
    <div>
      <br></br>
      <br></br>
      <div>
        <div style={{ height: 5 }}>
          <Autocomplete
            inputValue={inputValue}
            onInputChange={(event, newInputValue) => {
              setInputValue(newInputValue);
              setDatewise({ ...datewise, firstName: newInputValue });
            }}
            id="controllable-states-demo"
            options={customer}
            getOptionLabel={(option) => option.name}
            style={{ width: 250 }}
            renderInput={(params) => <TextField {...params} label="Search Name" variant="outlined" size="small" />}
          />
        </div>


        <form onSubmit={handleForm} class="form-inline">
          <label for="fromDate" style={{ marginLeft: 275 }}>From:</label>
          <input type="date" defaultValue={datewise.fromDate} id="fromDate" style={{ marginLeft: 5 }} onChange={(e) => {
            setDatewise({ ...datewise, fromDate: e.target.value })
          }} />
          <label for="todayDate" style={{ marginLeft: 40 }}>To:</label>
          <input type="date"  defaultValue={datewise.toDate}id="todayDate" style={{ marginLeft: 5 }} onChange={(e) => {
            setDatewise({ ...datewise, toDate: e.target.value })
          }} />
          <Container  >
            <Button type="submit" color="success" >Search</Button>
          </Container>
        </form>
      </div>
      <br /><br />
      <div>
        <label for="endingBalance">Ending Balance:</label>
        <input type="text" id="endingBalance" defaultValue={dateWiseBalance.endingBalance} readOnly="true" readOnly="true" style={{ marginLeft: 5 }} />
      </div>
      <br />

      <Row>
        <Col md={6}>
          <Table className="mt-4">
            <thead>
              <tr>
                <th width="15%">Date</th>
                <th width="20%">Name</th>
                <th width="15%">Rs</th>
              </tr>
            </thead>
            <tbody>
              {datelist}
            </tbody>
          </Table>
        </Col>
        <Col md={6} >
          <Table className="mt-4 offset-2">
            <thead>
              <tr>
                <th width="15%">Date</th>
                <th width="20%">Name</th>
                <th width="15%">Rs</th>
              </tr>
            </thead>
            <tbody>
              {datedebitlist}
            </tbody>
          </Table>
        </Col>
      </Row>
    </div>
  )
}
export default CashBook;