import React, { useEffect, useState } from 'react'
import { Button, Container, Form, FormGroup, Input } from 'reactstrap'
import axios from "axios";
import base_url from './api/bootapi';
import { toast } from 'react-toastify';
import Customer from './Customer';

const UpdateName = (props) => {

    const [customer, setCustomer] = useState({});
    

    useEffect(() => {
        document.title = "Update Customer";
    }, []);
// to fetch current name and display in current name box
    // useEffect (async () =>{ 
    //     let result =await fetch(`${base_url}/customer/person/${props.match.params.id}`);
    //     result =  await result.json();
    //     setCustomer(result)
    // },[])

    //form handler
    const handleForm = (e) => {
        console.log(customer);
        postDatatoServer(customer);
        e.target.reset()
        e.preventDefault();
    }

    //creating function t post data to server
    const postDatatoServer = (data) => {
        axios.put(`${base_url}/customer/updateCustomer/${props.match.params.id}`, data).then(
            (response) => {
                console.log(response);
                 {
                    toast.success("Customer has been updated successfully", {
                        position: "bottom-center",
                    });
                }
            }, (error) => {
                console.log(error);
                console.log("error");
            }
        )
    }


     return (
        <div>
            <h1 className="text-center">Update Customer</h1>
            <br></br>
            <Form onSubmit={handleForm}>
                <FormGroup>
                    <label for="firstName" style={{ width: 300 }}>FirstName</label>
                    <Input type="text" placeholder="Enter FirstName" name="firstName" id="firstName" required maxlength="45" onChange={(e) => {
                        setCustomer({ ...customer, name: e.target.value.toUpperCase() });
                    }} style={{ width: 300 }} />
                </FormGroup>
                <br></br>
                <Container>
                    <Button type="submit" color="success"  >Update Customer</Button>
                    &nbsp;&nbsp;&nbsp;
                    <Button type="reset" color="warning">Clear</Button>
                </Container>
            </Form>
        </div>
    );
};
export default UpdateName;