import React from 'react'
import { ListGroup} from 'reactstrap';
import { Link } from 'react-router-dom';


const Menus=() =>{
    return(
        <ListGroup>
            <Link className="list-group-item list-group-item-action" tag="a" to="/">Home</Link>

            <Link className="list-group-item list-group-item-action" tag="a" to="/view-customers">View Customers</Link>

            <Link className="list-group-item list-group-item-action"tag="a" to="/add-customer">Add Customer</Link>

            <ListGroup horizontal>
            <Link className="list-group-item list-group-item-action"tag="a" to="/Balance-CashReceipt" >Cash Receipt</Link>
            <Link className="list-group-item list-group-item-action"tag="a" to="/Balance-CashPayment" >Cash Payment</Link>
            </ListGroup>

            <Link className="list-group-item list-group-item-action"tag="a" to="/Balance-CashBook" >Ledger</Link>

            <Link className="list-group-item list-group-item-action"tag="a" to="/Balance" >CashBook</Link>

            <Link className="list-group-item list-group-item-action"tag="a" to="#!">About</Link>

            <Link className="list-group-item list-group-item-action"tag="a" to="#!">Contact Us</Link>
        </ListGroup>
    );
};
export default Menus;