import React, {useState} from 'react'
import "./SearchBar.css";
import SearchIcon from '@material-ui/icons/Search';
import { Link } from 'react-router-dom';


function SearchBar({placeholder,data, handleChange}){
    
    const[filteredData,setFilteredData]=useState([])
   
    const handleFilter=(e) => {
        const searchWord =e.target.value
        const newFilter = data.filter((value)=>{
            return value.firstName.toUpperCase().includes(searchWord.toUpperCase())
        })
        if (searchWord === ""){
             setFilteredData([]);
        }else{
           setFilteredData(newFilter) 
        }
     }
     let input=document.getElementById("searchName");
     if(input != null){
        input.addEventListener("keydown", function(event) {
        if (event.keyCode === 13) {
            event.preventDefault();
            document.getElementById("description").focus()
        }
    })};

    return(
        <div className="search">
                <div className="searchInputs">
                    <input type="text" id="searchName" onChange={handleFilter} autoFocus/>
                   </div>
                {filteredData.length !=0 && (
                <div className="dataResult">
                    {filteredData.slice(0,15).map((value,key)=>{
                        return (
                           
                          <a className="dataItem" href={value.receiptLink}   target="_self">
                              <p>{value.firstName}</p>
                          </a>
                     ); 
                     })}
                </div>
                )}
        </div>
    )
}
export default  SearchBar;