import React from 'react';
import './App.css';
import Header from './Header';
import Home from './Home';
import{Container,Row,Col} from "reactstrap";
import { ToastContainer} from 'react-toastify';
import AllCustomers from './AllCustomers';
import AddCustomer from './AddCustomer';
import Menus from './Menus';
import {BrowserRouter as Router,Route} from "react-router-dom";
import ViewCustomer from './ViewCustomer';
import AddEntry from './AddEntry';
import CashBook from './CashBook';
import CashReceipt from './CashReceipt';
import Ledger from './Ledger';
import SearchBar from './SearchBar';
import Receipt from './Receipt';
import Payment from './Payment';
import 'react-app-polyfill/stable'
import UpdateName from './UpdateName';

function App(){
 

  return( 
  <div>
   <Router>
   <ToastContainer />
       <Container>
        <Header />
       
 <Row>
          <Col md={4}>
              <Menus />
          </Col>
          <Col md={8}>
            <Route path="/" component={Home} exact />
            <Route path="/add-customer" component={AddCustomer} exact />
            <Route path="/view-customers" component={AllCustomers} exact />
            <Route path="/view-customer/:firstName" component={ViewCustomer} exact />
            <Route path="/update-customer/:firstName" component={AddEntry} exact />
            <Route path="/update-name/:id" component={UpdateName} exact />
            <Route path="/Balance" component={CashBook} exact />
            <Route path="/Balance-CashReceipt"  component={Receipt} exact />
            <Route path="/addreceipt-customer/:firstName" component={Receipt} exact />
            <Route path="/Balance-CashPayment"  component={Payment} exact />
            <Route path="/addpayment-customer/:firstName" component={Payment} exact />
            <Route path="/Balance-CashBook"  component={Ledger} exact />
          </Col>
        </Row>
       </Container>
   </Router>

     </div>
     );
 
}

export default App;