import React, { useEffect, useState } from 'react'
import { Button, Container, Form, FormGroup, Input } from 'reactstrap'
import axios from "axios";
import base_url from './api/bootapi';
import { toast } from 'react-toastify';
import { withRouter } from "react-router-dom";
import SearchBar from './SearchBar';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

const Receipt = () => {

    const [customer, setCustomer] = useState([]);
    const [person, setperson] = useState([]);
   
// for getting all customer list for the dropdown
    const getAllCustomers = () => {
        axios.get(`${base_url}/customer`).then(
            (response) => {
                console.log(response.data);
                if (response.data === []) {
                    toast.success("customer has been loaded", {
                        position: "bottom-center",
                    });
                }
                setCustomer(response.data);
            },

            (error) => {
                console.log(error);
                toast.error("something went wrong", {
                    position: "bottom-center",
                });
            }
        );
    };

    const getCustomerDetail = (inputValue) => {
        axios.get(`${base_url}/customer/${inputValue}`).then(
            (response) => {
                console.log(response.data);
                if (response.data === []) {
                    toast.success("No customer found", {
                        position: "bottom-center",
                    });
                }
                setperson(response.data);
            },

            (error) => {
                console.log(error);
               
            }
        );
    };

    useEffect(() => {
        getAllCustomers();
        document.title = "Cash Receipt"
    }, []);


    //  useEffect(async () => {
    //     let result = await fetch(`${base_url}/customer/${inputValue}`);
    //     result = await result.json();
    //     setperson(result)
    // }, [])





    //form handler
    const handleForm = (e) => {
        console.log(person);
        postDatatoServer(person);
        e.target.reset()
        document.getElementById("controllable-states-demo").focus()
        e.preventDefault();
    }

    //creating function t post data to server
    const postDatatoServer = (data) => {
        axios.put(`${base_url}/customer/`, data).then(
            (response) => {
                console.log(response);
                setperson({ ...person, totalAmountPending: response.data.totalAmountPending });
                document.getElementById("totalAmountPending").value = response.data.totalAmountPending
                toast.success("Entry has been added successfully", {
                    position: "bottom-center",
                });
            }, (error) => {
                console.log(error);
                console.log("error");
            }
        )
    }
    let buttonInput = document.getElementById("add");
    let input = document.getElementById("description");
    let inputs = document.getElementById("add");

    if (input != null && inputs != null) {
        input.addEventListener("keydown", function (event) {
            if (event.keyCode === 13) {
                event.preventDefault();
                document.getElementById("creditAmountReceived").focus()
            }
        });
        inputs.addEventListener("keydown", function (event) {
            if (event.keyCode === 13) {
                event.preventDefault();
                document.getElementById("description").focus()
            }
        });
    }
    function custom(event) {
        if (event.keyCode === 13) {
            event.preventDefault();
            document.getElementById("description").focus()
        }
    }
    const [inputValue, setInputValue] = React.useState('');

    return (
        <div>
            <h1 className="text-center">Add CashReceipt</h1>
            <br></br>

            <div style={{ height: 5 }}>
                <Autocomplete
                    inputValue={inputValue}
                    onInputChange={(event, newInputValue) => {
                        setInputValue(newInputValue);
                        getCustomerDetail(newInputValue)
                        //   setDatewise({...datewise,firstName:newInputValue});
                    }}
                    id="controllable-states-demo"
                    options={customer}
                    getOptionLabel={(option) => option.name}
                    style={{ width: 300 }}
                    renderInput={(params) => <TextField {...params} label="Search Name" variant="outlined" size="small" />}
                />
            </div>
            <br></br>
            <Form onSubmit={handleForm}>

                <br></br>

                <FormGroup>
                    <label for="description" style={{ width: 300 }}>Description</label>
                    <Input type="textarea" placeholder="Enter Description" name="description" id="description" maxlength="110"
                        onChange={(e) => {
                            setperson({ ...person, description: e.target.value });
                        }} style={{ width: 300 }} />
                </FormGroup>
                <br></br>

                <FormGroup>
                    <label for="totalAmountPending" style={{ width: 300 }}>Total Amount Pending</label>
                    <Input type="text" defaultValue={person.totalAmountPending} readOnly="totalAmountPending" id="totalAmountPending"
                        style={{ width: 300 }} />
                </FormGroup>
                <br></br>

                <FormGroup>
                    <label for="creditAmountReceived" style={{ width: 300 }}>Credit Amount</label>
                    <Input placeholder="Enter Credit Amount" name="creditAmountReceived" id="creditAmountReceived" required
                        onChange={(e) => {
                            setperson({ ...person, creditAmountReceived: e.target.value });
                        }} style={{ width: 300 }} />
                </FormGroup>
                <br></br>

                <FormGroup>
                    <label for="transactionEffectiveDate" style={{ width: 300 }}>Transaction Date</label>
                    <Input type="date" placeholder="Enter Date" defaultValue={person.transactionEffectiveDate} readOnly="true" name="transactionEffectiveDate" id="transactionEffectiveDate" required
                        style={{ width: 300 }} />
                </FormGroup>

                <Container>
                    <Button type="submit" color="success" id="add" >Add Entry</Button>
                    &nbsp;&nbsp;&nbsp;
                    <Button type="reset" color="warning">Clear</Button>
                </Container>
            </Form>
        </div>
    );
};
export default withRouter(Receipt);