import React,{useEffect, useState} from 'react'
import { Button, Container, Form, FormGroup, Input } from 'reactstrap'
import axios from "axios";
import base_url from './api/bootapi';
import { toast } from 'react-toastify';

const AddCustomer =() => {

//     const transactionData={
//         "name": "Rahil",
//         "customer":[
//      {
//       "firstName": "rahil",
//      "lastName": null,
//      "transactionEffectiveDate": null,
//      "totalAmountPending": 0,
//      "description": null,
//      "creditAmountReceived": 0,
//      "debitAmountReceived": 0,
//      "receiptLink": null,
//      "paymentLink": null,
//      "fromDate": null,
//      "toDate": null
//   }
// ]
    // }
    useEffect (() =>{
        document.title ="Add Customer";},[]);

    const[customer,setCustomer] = useState({});
   
    //form handler
    const handleForm=(e)=>{
        console.log(customer);
        postDatatoServer(customer);
        e.target.reset()
       e.preventDefault();
    }

    //creating function t post data to server
    const postDatatoServer= (data) =>{
        axios.post(`${base_url}/customer`,data).then(
            (response)=>{
                console.log(response);
                if(response.data === "Customer Already Exists" ){
                    toast.error("Customer Already Exists",{
                        position:"bottom-center",
                    })
                }else{  toast.success("Customer has been added successfully",{
                    position:"bottom-center",
                });}
                
              
            },(error)=>{
                console.log(error);
                console.log("error");
            }
        )
    }

   
    return (
        <div>
            <h1 className="text-center">Add Customer</h1>
            <br></br>

            <Form onSubmit={handleForm}>
                <FormGroup>
                    <label for="firstName" style={{ width:300 }}>FirstName</label>
                    <Input type="text" placeholder="Enter FirstName"  name="firstName" id="firstName" required maxlength="45"  onChange={(e) => {
                        setCustomer({...customer, name: e.target.value.toUpperCase() });
                    }} style={{ width:300 }}/>
                </FormGroup>
                <br></br>

               
                 <Container>
                     <Button  type="submit" color="success"  >Add Customer</Button>
                     &nbsp;&nbsp;&nbsp;
                     <Button type ="reset" color="warning">Clear</Button>
                 </Container>
            </Form>
            </div>
    );
};
export default AddCustomer;